.compare-view {
  background: #fbfbfb;
}
.compare-view .container {
  width: calc(100% - 150px);
  overflow-x: scroll;
  overflow-y: visible;
  margin-left: 150px;
  padding: 0;
  height: 100%;
  border-left: 1px solid #dadada;
  background: white;
}
.compare-view .price {
  color: #cf1322;
}
.compare-view table {
  border-collapse: collapse;
}
.compare-view td,
.compare-view th {
  margin: 0;
  border-top: 1px solid #dadada;
  padding: 5px;
  vertical-align: top;
}
.compare-view .headcol {
  position: absolute;
  width: 150px;
  left: 0;
  top: auto;
  padding-left: 16px;
}
